import React from 'react';
import { FiPlus } from 'react-icons/fi'

import './style.scss';

class Header extends React.Component {

  componentDidMount() {
    var isFirefox = typeof InstallTrigger !== 'undefined';
    if (isFirefox) {
      let svg = document.querySelector('svg');
      svg.classList.add('firefox')
    }
  }
  render() {
    return (
      <header className="header">
        <div className="main">
          <h1 className="hi">HI! I'AM</h1>
          <div className="icon">
            <h1 className="first-name">FERNANDO</h1>
            <FiPlus color="#fff" />
          </div>
          <div className="break">
            <h1 className="last-name">FREGATE</h1>
            <h2 className="description">A CREATIVE <br />ART DIRECTOR <br />BASED IN <br />SAO PAULO</h2>
          </div>
        </div>
        <div className="about">
          <a href="about-me" className="me">ABOUT ME</a>
        </div>
      </header>
    );
  }
}

export default Header;